import { Component, OnInit } from '@angular/core';
import { LocalDataSource } from 'ng2-smart-table';
import { ReportService, IAnswer } from '../../../@core/data/report.service';
import { DatePipe } from '@angular/common';
@Component({
    selector: 'ngx-notfound-conversation',
    templateUrl: './notfound-conversation.component.html',
    styleUrls: ['./notfound-conversation.component.scss'],
})
export class NotFoundConversationComponent implements OnInit {
    answers: IAnswer[] = [];
    settings = {
        mode: 'external',
        editable: false,
        actions: {
            add: false,
            edit: false,
            delete: false,
        },
        add: {
            addButtonContent: '<i class="nb-plus"></i>',
            createButtonContent: '<i class="nb-checkmark"></i>',
            cancelButtonContent: '<i class="nb-close"></i>',
        },
        edit: {
            editButtonContent: '<i class="nb-edit"></i>',
            saveButtonContent: '<i class="nb-checkmark"></i>',
            cancelButtonContent: '<i class="nb-close"></i>',
        },
        delete: {
            deleteButtonContent: '<i class="nb-trash"></i>',
        },
        columns: {
            question: {
                title: 'Question',
                type: 'string',
            },
            rawAnswer: {
                title: 'Answer',
                type: 'string',
            },
            confident: {
                title: 'Confident',
                type: 'number',
            },
            timeStamp: {
                title: 'Time Stamp',
                type: 'string',
            },
        },
    };

    source: LocalDataSource = new LocalDataSource();
    constructor(private service: ReportService,
        private datePipe: DatePipe,
    ) {

    }

    ngOnInit() {
        this.getConversationList();
    }

    getConversationList() {
        this.service.getAnswers().subscribe(resp => {
            resp.forEach(x => {
                this.answers.push({
                    compiledAnswer: x.compiledAnswer,
                    confident: x.confident,
                    question: x.question,
                    rawAnswer: x.rawAnswer,
                    timeStamp: this.datePipe.transform(new Date(x.timestamp), 'shortDate'),
                })
            });
            this.answers = this.answers.filter(function (x) {
                return x.rawAnswer === 'No Match';
            });
            this.source.load(this.answers);
        });
    }
}
