import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ReportComponent } from './report.component';
import { AllConversationComponent } from './all-conversation/all-conversation.component';
import { TodayConversationComponent } from './today-conversation/today-conversation.component';
import { NotFoundConversationComponent } from './notfound-conversation/notfound-conversation.component';

const routes: Routes = [{
    path: '',
    component: ReportComponent,
    children: [{
        path: 'all-conversation',
        component: AllConversationComponent,
    },
    {
        path: 'today-conversation',
        component: TodayConversationComponent,
    },
    {
        path: 'notfound-conversation',
        component: NotFoundConversationComponent,
    }],
}];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule],
})
export class ReportRoutingModule { }

export const routedComponents = [
    ReportComponent,
    AllConversationComponent,
    TodayConversationComponent,
    NotFoundConversationComponent,
];
